@keyframes showParagraph {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes titleAnimation {
  0% {
    transform: translateY(-60%);
    opacity: 0;
    clip-path: polygon(0% 50%, 100% 50%, 100% 100%, 0 100%);
  }
  20% {
    transform: translateY(0);
    opacity: 1;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0 100%);
  }
  80% {
    transform: translateY(0);
    opacity: 1;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0 100%);
  }
  100% {
    opacity: 1;
  }
}
